<template>
  <div v-if="this.mealArr" class="discount-container">
    <ul>
        <li v-for="(meal, index) in mealArr">
          <v-card :data="meal"></v-card>
        </li>
    </ul>
  </div>
</template>

<script>
import vCard from "./card.vue";
import { mapState } from "vuex";

export default {
  name: "discount-combine",
  data() {
    return {
      mealArr: null,
      isLoadDb: false
    };
  },
  components: {
    vCard
  },
  watch: {
    'token':"getMealData"
  },
  activated() {
    document.body.scrollTop = 0;
    if (this.isLoadDb) this.$store.dispatch("toggleLoading", false);
  },
  created() {
    this.getMealData();
  },
  beforeRouteLeave(to, from, next) {
    // this.$store.commit("UPDATE_NOBINDSHOW", false);
    next();
  },
  methods: {
    getMealData() {
      this.$http
        .post(this.path + `php/ttl/goodsFunction.php`, {
          type: "goodslist",
          userid: this.userid,
          openid: this.openid,
          page: 0
        })
        .then(response => {
          let data = response.body;
          let mealArr = [];
          for (var key in data) {
            if (key == "error") break;
            var serverItem = data[key];
            let mealItem = {};
            mealItem["type"] = serverItem.type;
            mealItem["price"] = serverItem.price;
            mealItem["cardId"] = serverItem.id + "";
            mealItem["name"] = serverItem.gname;
            mealItem["slogan"] = serverItem.message;
            mealItem["imgUrl"] = this.path + serverItem.icon;
            mealItem["hasBuy"] = !Boolean(serverItem.iscanbuy);
            mealArr.push(mealItem);
            this.mealArr = mealArr;
            // this.mealArr.length=1;
            this.isLoadDb = true;
            this.$store.dispatch("toggleLoading", false);
          }
        })
        .catch(response=>{
          //请求出错处理
          this.$store.dispatch("toggleLoading", false);
          console.log(response);
        });
    }
  },
  computed: {
    ...mapState(["openid", "path", "userid","token"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.discount-container {
    width: 100%;
    flex 1;
    padding-bottom .43rem;

    ul {
        padding: .1rem .1rem 0 .1rem;

        li {
            width: 100%;
            margin-bottom: 0.05rem;
            box-shadow: 0 1px 0px rgba(150, 150, 150, 0.1);
        }
    }
}
</style>



