<template>
  <div class="card">
    <my-image class="my-img fade" :imgUrl="data.imgUrl" @click.native="clickDetailsHandle"></my-image>
    <div class="card-content">
      <div class="left">
        <p class="name">{{data.name}}</p>
        <p class="slogan">{{data.slogan}}</p>
      </div>
      <div class="right">
        <mt-button size="small" class="btn info" type="default" @click.native="clickDetailsHandle">详情</mt-button>
        <mt-button disabled v-if="data.hasBuy" size="small" class="btn has-buy" type="default">已购买</mt-button>
        <mt-button v-else size="small" class="btn buy" type="default" @click.native="buyHandle">购买</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import wxpay from "../../../common//js/wxpay";
import myImage from "../../lazyloadImg/lazyImage.vue";
import Vue from "vue";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);
import { mapState } from "vuex";


export default {
  name: "v-card",
  props: {
    data: {
      type: Object
    }
  },
  components: {
    myImage
  },
  created() {
  },
  methods: {
    clickDetailsHandle(event) {
      this.$store.commit("UPDATE_CURMEALDATA", this.data);
      this.$router.push({ name: "details" });
    },
    buyHandle(event) {
      if (!this.token) {
        this.$router.push({path: '/login'});
        return;
      }
      if (parseInt(this.role) > 2) {
        Toast({
          message: "您的身份不能购买此产品！",
          iconClass: "iconfont icon-tishi1",
          duration: 2000
        });
      } else this.jumpToPayPage();
    },
    jumpToPayPage() {
      var cookieStr =
        "userid:" +
        this.userid +
        ",goodsid:" +
        this.data.cardId +
        ",num:1,types:GoodsPayParam";
      wxpay.GetPayCode(cookieStr);
    }
  },
  computed: {
    ...mapState(["openid", "role", "phoneno", "userid","token"])
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
.card {
  background: #fefefe;
  padding: 0.1rem 0.1rem 0 0.1rem;
  box-sizing: border-box;

  .my-img {
    width: 100%;
    height: 1.4rem;
    background-color: #f1f1f1;
  }

  .card-content {
    width: 100%;
    height: 0.5rem;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left {
      .name {
        font-size: 0.14rem;
        font-weight: 600;
        letter-spacing: 0.005rem;
      }

      .slogan {
        margin-top: 0.04rem;
        font-size: 0.13rem;
        color: #898989;
        letter-spacing: 0.005rem;
      }
    }

    .right {
      margin-top 0.04rem;
      .btn {
        font-size: 0.13rem;
        height: 0.28rem;
        box-shadow: none;
        letter-spacing: 0.005rem;

        &.info {
          margin-right: 0.03rem;
          background-color: #dcdddd;
          color: #a4a5a5;
        }

        &.has-buy {
          font-size: 0.13rem;
          // background-color: #e8e8e8;
          border 1px solid #dcdddd;
        }

        &.buy {
          background-color: #fff798;
          color: #595656;
        }
      }
    }
  }
}
</style>



